	.report-slider {
		// width: 100%

		.slick {

			&-arrow {
				top: 0;
				@include size(50px 100%);
				border: none;
				transform: none;
				background: rgba(#000, .2);
				transition: background .3s ease;
				z-index: 2;

				&:before {
					content: "";
					border: {
						top: 1px solid #fff;
						right: 1px solid #fff; }
					@include size(15px);
					position: absolute;
					top: calc(50% - 7px); }

				&:hover, &:focus {
					background: rgba(#000, .6); } }


			&-prev {
				left: -7px;

				&:before {
					transform: rotate(-135deg);
					left: calc(50% - 3px); } }

			&-next {
				right: -7px;

				&:before {
					transform: rotate(45deg);
					left: calc(50% - 12px); } } } } }

