@import "vars";
@import "functions";

@supports (display: grid) {
	.main-content {
		display: grid;
		grid-gap: rem(32) rem(30);
		grid-template-columns: repeat(3, 1fr);
		// // grid-auto-rows: 280px

		// &--tablet
		// 	grid-gap: 2rem
		// 	grid-template-columns: repeat(2, 1fr)

		&__item {
			margin-bottom: 0;
			width: auto;

			&--row {
				grid-column: span 2; }

			&--col {
				grid-row: span 2; }

			&-img {
				display: flex;
				flex-direction: column; } } }

	.events {
		display: grid;
		margin: 0 !important;
		grid-template-columns: repeat(3, 1fr);
		grid-gap: rem(32) rem(30);

		&__one {
			width: auto !important;
			margin: 0 !important;

			&--big {
				grid-row: span 2;
				grid-column: span 2; } } }

	.imgs {
		margin: 0 !important;
		width: auto !important;
		display: grid;
		grid-gap: calc(2rem - 2px);
		grid-template-columns: repeat(3, 1fr);

		&__one {
			width: auto !important;
			margin: 0 !important;

			&.vertical-span {
				grid-row: span 2; } } } }
