.radio {
	position: relative;

	&:hover, .radio__input:focus + {

		.radio {

			&__label {
				color: $main-color;

				&:before {
					border-color: $main-color; } } } }

	&__input {
		position: absolute;
		top: 0;
		left: 0;
		opacity: 0;
		width: 0;
		height: 0;

		&:checked {

			+ label {

				&:after {
					opacity: 1; } } } }

	&__label {
		position: relative;
		min-height: 26px;
		display: flex;
		align-items: center;
		cursor: pointer;
		padding-left: 40px;
		user-select: none;
		transition: color .3s ease;

		&:before, &:after {
			content: "";
			position: absolute;
			border-radius: 50%; }

		&:before {
			top: 0;
			left: 0;
			background: $second-color;
			width: 26px;
			height: 26px;
			transition: border-color .3s ease; }

		&:after {
			top: 9px;
			left: 9px;
			width: 8px;
			height: 8px;
			transition: opacity .3s ease;
			opacity: 0;
			z-index: 1;
			background: $main-color; } } }

.checkbox {
	position: relative;

	&:hover, .checkbox__input:focus + {

		.checkbox {

			&__label {
				color: $main-color;

				&:before {
					border-color: $main-color; } } } }

	&__input {
		position: absolute;
		top: 0;
		left: 0;
		opacity: 0;
		width: 0;
		height: 0;

		&:checked {

			+ label {

				&:after {
					opacity: 1; } } } }

	&__label {
		position: relative;
		padding-left: 32px;
		cursor: pointer;
		min-height: 18px;
		display: flex;
		align-items: center;
		transition: color .3s ease;

		&:before, &:after {
			position: absolute;
			content: ""; }

		&:before {
			background: #fff;
			width: 18px;
			height: 18px;
			left: 0;
			top: 2px;
			top: 0;
			border: 1px solid $border-color;
			transition: border-color .3s ease; }

		&:after {
			transition: opacity .3s ease;
			width: 11px;
			height: 6px;
			left: 4px;
			top: 8px;
			top: 6px;
			background: url("../img/ico-check.png") no-repeat;
			opacity: 0; } } }

