.main-slider {
	// height: vh(857)
	// min-height: 660px

	&.js__have-slides, &.js__have-slides a.main-slider__slide-desc {
		cursor: none; }

	&--dotted {

		.slick-dots {
			position: absolute;
			bottom: 75px;
			left: 0;
			right: 0;
			display: flex;
			justify-content: center;

			button {
				text-indent: -9999px;
				overflow: hidden;
				border: none;
				padding: 0;
				@include size(6px);
				transition: transform .3s ease;
				border-radius: 50%; }

			.slick-active {

				button {
					transform: scale(1.7); } }

			li {

				&:not(:last-child) {
					margin-right: 27px; } } } }


	&.slider-loaded {

		.slick-slide.slick-active {

			&:first-child {

				.main-slider {

					&__slide {

						&-title, &-text {
							opacity: 1;
							transform: translate3d(0, 0, 0); }

						&-img {

							img, picture {
								opacity: 1;
								transform: scale3d(1, 1, 1);
								user-select: none; } } } } } }

		img, picture {
			transition-delay: .2s;
			// transform: scale3d(1.2, 1.2, 1.2)
			opacity: 1; }

		.main-slider {

			&__slide-desc {
				opacity: 1; } } }

	&:not(.slick-initialized) {
		opacity: 0;

		// .main-slider

		// 	&__slide-desc
 }		// 		opacity: 0

	&__cont {
		position: relative;
		background: #000;

		// &:after
		// 	content: ""
		// 	position: absolute
		// 	left: 0
		// 	top: calc(100% - 243px)
		// 	background: url("../img/main-slider-shadow.png") center repeat-x
		// 	+size(100% 485px)
 }		// 	pointer-events: none

	&__slide-desc {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;

		.wrapper {
			width: 100%; } }

	&__slide-desc {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		transition: opacity .3s ease;
		opacity: 0; }

	// &__arrows
	// 	z-index: 2
	// 	// pointer-events: none
	// 	bottom: rem(115)
	// 	// z-index: 50
	// 	cursor: none
	// 	display: flex
	// 	flex-direction: column

	.slick {

		&-arrow {
			// pointer-events: all
			@include size(28px);
			border: {
				top: 2px solid #fff;
				right: 2px solid #fff; }
			background: none;
			position: absolute;
			// transition: opacity .3s ease
			// opacity: 0
			cursor: none;
			z-index: 4; }

		&-next {
			transform: rotate(45deg); }

		&-prev {
			transform: rotate(-135deg); }

		// .wrapper
		// 	// display: flex
		// 	// justify-content: space-between
		// 	// align-items: center
		// 	position: relative
		// 	cursor: none
 }		// 	flex: 1 1 auto

	.slick {

		&-track {
			display: flex; }

		&-slide {
			flex: none;
			height: auto !important;
			display: flex !important;
			flex-direction: column;

			> div {
				height: auto;
				flex: 1 1 auto;
				display: flex;
				flex-direction: column; } }

		&-list, &-track, &-slide, &-slide > div {
			height: 100%;
			max-height: 100%; }

		&-active {

			// &:not(:first-child)

			.main-slider__slide {

				&-title, &-text {
					opacity: 1;
					transform: translate3d(0, 0, 0); }

				&-img {

					img, picture {
						opacity: 1;
						transform: scale3d(1, 1, 1);
						user-select: none; } } } } }

	&__slide {
		text-align: center;
		font-weight: 400;
		position: relative;
		display: flex !important;
		// height: 100%
		// max-height: 100%
		flex: 1 1 auto;
		flex-direction: column;

		&:not(:first-child) {
			display: none; }

		&-video {
			overflow: hidden;

			video {
				width: 100%;
				display: block;
				max-height: calc(100vh - 100px);
				// margin: vh(-120) 0
				max-height: calc(100vh - 100px);
				object-fit: cover;
				object-position: center; }

			+ .main-slider__slide-img {
				display: none; } }

		@media (max-width: 1000px) {
			&-video {

				video {
					height: calc(100vh - 75px);
					max-height: none; }

				&:not(:last-child) {
					display: none;

					+ .main-slider__slide-img {
						display: block; } } } }

		&-img {
			position: relative;
			// overflow: hidden
			// height: 100%
			// max-height: 100%
			flex: 1 1 auto;
			flex-direction: column;
			display: flex;

			&:after {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				background: rgba(#000, .5); }

			img, picture {
				display: block;
				transition: opacity 1s ease, transform 1s ease;
				opacity: 0;
				transform: scale3d(1.2, 1.2, 1.2);
				width: 100%;
				flex: 1 1 auto;
				max-height: calc(100vh - 120px); }

			picture {
				display: flex;
				flex-direction: column; }

			img {
				object: {
					fit: cover;
					position: center; } } }

		&-desc {
			z-index: 1;
			bottom: 48px;
			padding-top: rem(40);
			font-family: "Formular"; }

		&-title, &-text {
			transition: opacity 1s ease, transform 1s ease;
			opacity: 0;
			max-width: 650px;
			margin: {
				left: auto;
				right: auto; } }

		&-title {
			font-size: rem(24);
			margin-top: 16px;
			letter-spacing: .02em;
			transform: translate3d(-50px, 0, 0); }

		&-text {
			font-size: rem(34);
			letter-spacing: .03em;
			transform: translate3d(50px, 0, 0);
			font-weight: 700; } } }

